@charset "UTF-8";
.accordeon {
  border-top: 1px solid #00fff7;
  margin-bottom: 1rem;
  background: #0a0a0a;
}
.accordeon-title {
  padding: 1rem;
  background: #101010;
  color: #00fff7;
  font-weight: bold;
  cursor: pointer;
  position: relative;
  font-size: 0.8rem;
}
.accordeon-title::after {
  content: "⯈";
  position: absolute;
  right: 1rem;
  transform: rotate(0deg);
  transition: transform 0.2s ease;
}
.accordeon-content {
  max-height: 0;
  overflow: hidden;
  transition: max-height 0.35s ease;
  padding: 0 1rem;
}
.accordeon.active .accordeon-title::after {
  transform: rotate(90deg);
}

.language-switcher {
  display: flex;
  align-items: center;
  justify-content: center;
  margin: 0.4rem 0;
  gap: 0.5rem;
}
.language-switcher select {
  background: #111827;
  color: #0ff;
  border: 1px solid rgba(0, 255, 255, 0.4);
  padding: 0.3rem 0.6rem;
  border-radius: 4px;
  font-family: monospace;
  font-size: 0.9rem;
  cursor: pointer;
  box-shadow: 0 0 5px rgba(0, 255, 255, 0.2);
}
.language-switcher select:focus {
  outline: none;
  border-color: cyan;
  box-shadow: 0 0 8px cyan;
}

.bq-toast-container {
  position: fixed;
  z-index: 2000;
  display: flex;
  flex-direction: column;
  gap: 0.5rem;
  pointer-events: none;
  padding: 1rem;
}
.bq-toast-container.top-right {
  top: 0;
  right: 0;
  align-items: flex-end;
}
.bq-toast-container.top-left {
  top: 0;
  left: 0;
  align-items: flex-start;
}
.bq-toast-container.bottom-right {
  bottom: 0;
  right: 0;
  align-items: flex-end;
}
.bq-toast-container.bottom-left {
  bottom: 0;
  left: 0;
  align-items: flex-start;
}
.bq-toast-container.top-center {
  top: 0;
  left: 50%;
  transform: translateX(-50%);
  align-items: center;
}
.bq-toast-container.bottom-center {
  bottom: 0;
  left: 50%;
  transform: translateX(-50%);
  align-items: center;
}

.bq-toast {
  display: flex;
  align-items: center;
  gap: 1rem;
  background: #111827;
  color: #fff;
  border: 1px solid rgba(0, 255, 255, 0.2666666667);
  padding: 0.75rem 1rem;
  border-radius: 6px;
  font-size: 0.95rem;
  box-shadow: 0 0 12px rgba(0, 255, 255, 0.15);
  max-width: 320px;
  min-width: 200px;
  pointer-events: auto;
  opacity: 0;
  transform: translateY(20px);
  transition: all 0.3s ease;
}
.bq-toast.show {
  opacity: 1;
  transform: translateY(0);
}
.bq-toast .bq-toast-icon {
  font-size: 1.2rem;
}
.bq-toast .bq-toast-message {
  flex: 1;
  line-height: 1.3;
}
.bq-toast .bq-toast-close {
  background: none;
  border: none;
  color: inherit;
  font-size: 1rem;
  cursor: pointer;
  opacity: 0.6;
  transition: opacity 0.2s ease;
  padding: 0;
}
.bq-toast .bq-toast-close:hover {
  opacity: 1;
}
.bq-toast.success {
  border-color: #22c55e;
  background: rgba(34, 197, 94, 0.1);
  color: #bbf7d0;
}
.bq-toast.error {
  border-color: #ef4444;
  background: rgba(239, 68, 68, 0.1);
  color: #fecaca;
}
.bq-toast.info {
  border-color: #3b82f6;
  background: rgba(59, 130, 246, 0.1);
  color: #bfdbfe;
}
.bq-toast.warning {
  border-color: #facc15;
  background: rgba(250, 204, 21, 0.1);
  color: #fde68a;
}
.bq-toast.primary {
  border-color: cyan;
  background: rgba(0, 255, 255, 0.1);
  color: cyan;
}
.bq-toast.secondary {
  border-color: #64748b;
  background: rgba(100, 116, 139, 0.1);
  color: #cbd5e1;
}

.debug-panel {
  position: fixed;
  background: #111;
  color: #0ff;
  border-top: 4px solid rgba(0, 255, 255, 0.4);
  padding: 1rem;
  z-index: 9999;
  display: none;
  transition: box-shadow 0.2s;
  top: 50%;
  left: 50%;
}
.debug-panel .kv-section {
  margin-top: 10px;
  border-top: 1px solid #999;
  padding-top: 5px;
}
.debug-panel h3 {
  margin: 0;
  margin-bottom: 1rem;
  font-size: 1rem;
  color: rgba(0, 255, 255, 0.4);
}
.debug-panel .wrap {
  margin: 0.5rem;
}
.debug-panel.active {
  display: block;
}
.debug-panel.dragging {
  cursor: move;
  user-select: none;
}
.debug-panel .close-btn {
  position: absolute;
  right: 6px;
  top: 6px;
  font-size: 1.4rem;
  background: none;
  border: 1px solid #f88;
  color: #f88;
  cursor: pointer;
  transition: color 0.2s, transform 0.2s;
  border-radius: 50%;
  text-align: center;
  line-height: 14px;
  width: 20px;
  height: 20px;
}
.debug-panel .close-btn:hover {
  color: red;
  transform: rotate(180deg);
}
.debug-panel .debug-line {
  font-family: monospace;
  font-size: 12px;
  margin: 2px 0;
}
.debug-panel .debug-line strong {
  color: #ffcc00;
}
.debug-panel .debug-table {
  width: 100%;
  border-collapse: collapse;
  margin-top: 10px;
  max-height: 400px;
  overflow: auto;
}
.debug-panel .debug-table th, .debug-panel .debug-table td {
  border: 1px solid #999;
  padding: 6px 10px;
  font-size: 13px;
  font-family: monospace;
}
.debug-panel .debug-table td.truncated {
  max-width: 400px;
  overflow: auto;
}
.debug-panel .debug-table th {
  background-color: #222;
  text-align: left;
  color: rgba(0, 255, 255, 0.4);
  font-weight: bold;
}
.debug-panel .debug-table tr:nth-child(even) {
  background-color: #333;
}
.debug-panel .debug-table tfoot td {
  font-weight: bold;
  background-color: #222;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: "Helvetica Neue", Arial, sans-serif;
  font-size: 16px;
  line-height: 1.5;
  color: #333;
  background-color: #fff;
}

img {
  max-width: 100%;
  height: auto;
  display: block;
}

a {
  text-decoration: none;
  color: inherit;
}

h1, h2, h3, h4, h5, h6 {
  font-weight: bold;
  line-height: 1.2;
  margin-bottom: 1rem;
  color: #333;
}

p {
  margin-bottom: 1rem;
}

.text-center {
  text-align: center;
}

.mt-1 {
  margin-top: 1rem;
}

.mb-1 {
  margin-bottom: 1rem;
}

.p-1 {
  padding: 1rem;
}

.page-wrapper {
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  background-color: #f7f7f9;
}

header {
  background-color: #1a1a2e;
  color: white;
  padding: 1rem 2rem;
}
header h1 {
  font-size: 2rem;
  margin: 2rem 1rem;
  text-align: center;
  color: #fff;
}
header .logo {
  font-weight: bold;
  font-size: 1.25rem;
}
header nav a {
  color: #fff;
  margin-left: 1rem;
  text-decoration: none;
  font-weight: 500;
}
header nav a:hover {
  color: #0ff;
}

.sidebar {
  background-color: #222;
  color: #eee;
  padding: 2rem 1rem;
  width: 250px;
  min-height: 100vh;
  position: sticky;
  top: 0;
}
.sidebar ul {
  list-style: none;
  padding: 0;
}
.sidebar ul li {
  margin-bottom: 1rem;
}
.sidebar ul li a {
  color: #ccc;
  text-decoration: none;
}
.sidebar ul li a:hover {
  color: #0ff;
}

.main {
  flex: 1;
  padding: 2rem;
  background-color: #fff;
}

.breadcrumb {
  display: block;
  margin: 2rem 0;
  font-size: 0.95rem;
  font-weight: 500;
  color: #666;
}
.breadcrumb ul {
  display: flex;
  flex-wrap: wrap;
  list-style: none;
  padding: 0;
  margin: 0;
  gap: 0.75rem;
}
.breadcrumb li {
  display: flex;
  align-items: center;
  gap: 0.5rem;
}
.breadcrumb li::after {
  content: "›";
  color: #aaa;
  font-size: 0.85em;
}
.breadcrumb li:last-child::after {
  content: "";
}
.breadcrumb a {
  text-decoration: none;
  color: #007bff;
  transition: color 0.3s;
}
.breadcrumb a:hover {
  color: #0056b3;
  text-decoration: underline;
}
.breadcrumb span {
  font-weight: bold;
  color: #aaa;
}

footer {
  background-color: #1a1a2e;
  color: #aaa;
  text-align: center;
  padding: 1rem;
  margin-top: auto;
}

@media (max-width: 768px) {
  .page-wrapper {
    flex-direction: column;
  }
  .sidebar {
    width: 100%;
    position: relative;
    min-height: auto;
  }
  .main {
    padding: 1rem;
  }
  header nav a {
    margin-left: 0.5rem;
  }
}
.container {
  max-width: 1200px;
  margin: 0 auto;
  padding: 1rem;
  width: 100%;
  min-height: 200px;
}

.wrapper {
  display: flex;
  flex-wrap: wrap;
  gap: 1rem;
  min-height: 200px;
  padding: 2rem 0;
}

.column {
  flex: 1;
  min-width: 200px;
  padding: 1rem;
  background: #f5f5f5;
  border: 1px dashed #ccc;
  min-height: 200px;
}

.two-column {
  display: flex;
  gap: 1rem;
  min-height: 200px;
}
.two-column > .column {
  flex: 1 1 50%;
}

.three-column {
  display: flex;
  gap: 1rem;
  min-height: 200px;
}
.three-column > .column {
  flex: 1 1 33.3333%;
}

.section {
  padding: 3rem 1rem;
  background-color: #fafafa;
  border: 1px solid #eee;
  min-height: 200px;
}

.grid {
  display: grid;
  gap: 1rem;
  grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
  min-height: 200px;
}

.cell {
  padding: 1rem;
  background-color: #f0f0f0;
  border: 1px dashed #bbb;
  min-height: 200px;
}